import clientPromise from "@/lib/mongodb";
import { NextResponse } from "next/server";

export async function POST(request) {
  try {
    const body = await request.json();
    console.log("Request body:", body);
    console.log("Username:", body.username);

    if (!body.username) {
      return NextResponse.json({ success: false, message: "Username is required" });
    }

    const client = await clientPromise;
    const db = client.db("Linktree");
    const collection = db.collection("Links");

    const userLinks = await collection.findOne({ username: body.username });
    console.log("User links found:", userLinks);

    if (!userLinks) {
      return NextResponse.json({ success: false, message: "User not found" });
    }

    return NextResponse.json({ success: true, data: userLinks });
  } catch (error) {
    console.error("Error in /api/links:", error);
    return NextResponse.json({ success: false, message: "Internal server error" });
  }
}